.home-page {
  min-height: calc(100vh - 140px);
}

/* Hero Section */
.hero-section {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: white;
  padding: 4rem 2rem;
  text-align: center;
  margin-bottom: 3rem;
}

.hero-content h1 {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: white;
}

.hero-content p {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 3rem;
  margin-top: 2rem;
}

.hero-stats .stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 1rem;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Search and Filter */
.search-filter {
  background: white;
  padding: 2rem;
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow-md);
  margin: -2rem 2rem 3rem 2rem;
  position: relative;
  z-index: 10;
}

.filter-group {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.search-input,
.filter-select {
  padding: 0.75rem 1rem;
  border: 2px solid #e5e7eb;
  border-radius: var(--border-radius-sm);
  font-size: 1rem;
  transition: var(--transition);
  background: white;
  min-width: 180px;
}

.search-input:focus,
.filter-select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  outline: none;
}

.search-input {
  min-width: 250px;
}


/* Courses Section */
.courses-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem 4rem 2rem;
}

.section-header {
  text-align: center;
  margin-bottom: 3rem;
}

.section-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.course-count {
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.courses-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 2rem;
  list-style: none;
  padding: 0;
  margin: 0;
}

.course-card {
  background: var(--background-primary);
  border: 2px solid #e5e7eb;
  border-radius: var(--border-radius-md);
  padding: 0;
  box-shadow: var(--box-shadow-sm);
  transition: var(--transition);
  overflow: hidden;
  position: relative;
}

.course-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-color), var(--primary-light));
}

.course-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border-color: var(--primary-light);
}

.course-header {
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}

.course-header h3 {
  margin: 0;
  color: var(--text-primary);
  font-size: 1.3rem;
  font-weight: 600;
  line-height: 1.3;
  flex: 1;
}

.course-category {
  background: var(--primary-color);
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  flex-shrink: 0;
}

.course-description {
  padding: 0 1.5rem;
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.5;
  margin-bottom: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.course-meta {
  padding: 0 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
}

.course-instructor {
  color: var(--text-secondary);
  font-style: italic;
}

.course-rating {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.rating-stars {
  color: #fbbf24;
  font-size: 0.9rem;
}

.rating-number {
  color: var(--text-secondary);
  font-size: 0.85rem;
}

.course-footer {
  padding: 1rem 1.5rem 1.5rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f9fafb;
  border-top: 1px solid #f3f4f6;
}

.course-price {
  font-weight: 700;
  color: var(--success-color);
  font-size: 1.3rem;
}

.course-link {
  background: var(--primary-color);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius-sm);
  text-decoration: none;
  font-weight: 500;
  transition: var(--transition);
}

.course-link:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
  color: white;
  text-decoration: none;
}

/* No courses state */
.no-courses {
  text-align: center;
  padding: 4rem 2rem;
  background: #f9fafb;
  border-radius: var(--border-radius-md);
  border: 2px dashed #d1d5db;
}

.no-courses h3 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.no-courses p {
  color: var(--text-secondary);
  font-size: 1.1rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .courses-list {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
  }
}

@media (max-width: 768px) {
  .hero-section {
    padding: 2rem 1rem;
  }
  
  .hero-content h1 {
    font-size: 2rem;
  }
  
  .hero-content p {
    font-size: 1.1rem;
  }
  
  .search-filter {
    margin: -2rem 1rem 2rem 1rem;
    padding: 1.5rem;
  }
  
  .filter-group {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .search-input,
  .filter-select {
    width: 100%;
    min-width: auto;
  }
  
  .courses-section {
    padding: 0 1rem 2rem 1rem;
  }
  
  .courses-list {
    grid-template-columns: 1fr;
  }
  
  .section-header h2 {
    font-size: 2rem;
  }
  
  .course-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .course-footer {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 1.75rem;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .hero-stats {
    gap: 1.5rem;
  }
}
