/* About page styles */
.about-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2.5rem;
  background: var(--background-primary);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow-md);
}

.about-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--primary-dark);
}

.about-container p {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 2rem;
  color: var(--text-secondary);
}

.about-container h2 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.about-container ul {
  font-size: 1rem;
  line-height: 1.8;
  padding-left: 1.2rem;
  color: var(--text-secondary);
}

.about-container ul li {
  margin-bottom: 0.5rem;
}