/* Footer styles */
.footer {
  background: var(--background-primary);
  color: var(--text-secondary);
  text-align: center;
  padding: 1.5rem 0;
  font-size: 0.9rem;
  border-top: 1px solid #e5e7eb;
}

.footer p {
  margin: 0;
}

.footer a {
  color: var(--primary-color);
  text-decoration: none;
  margin: 0 0.5rem;
  transition: var(--transition);
}

.footer a:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}