/* Modernized styles for course details page */
.course-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2.5rem;
  background: var(--background-primary);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow-md);
}

.course-details h2 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  color: var(--primary-dark);
}

.course-details p {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  color: var(--text-secondary);
}

.course-details strong {
  color: var(--text-primary);
}

.course-actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-top: 1.5rem;
}

.go-to-course-btn, #enrollBtn, #downloadCertBtn, .edit-course-btn, #submitReviewBtn {
  background-color: var(--primary-color);
  color: white;
  padding: 12px 20px;
  border-radius: var(--border-radius-sm);
  font-weight: 600;
  border: none;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  transition: var(--transition);
}

.go-to-course-btn:hover, #enrollBtn:hover, #downloadCertBtn:hover, .edit-course-btn:hover, #submitReviewBtn:hover {
  background-color: var(--primary-dark);
}

.reviews {
  margin-top: 2.5rem;
}

.reviews h3 {
  font-size: 1.8rem;
  margin-bottom: 1.2rem;
  color: var(--primary-dark);
}

#reviewsList > div {
  background: var(--background-secondary);
  padding: 1rem;
  border-radius: var(--border-radius-sm);
  margin-bottom: 1rem;
  box-shadow: var(--box-shadow-sm);
  border: 1px solid #e5e7eb;
}

#reviewRating, #reviewComment {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border-radius: var(--border-radius-sm);
  border: 1px solid #d1d5db;
  font-size: 1rem;
  font-family: inherit;
  transition: var(--transition);
}

#reviewRating:focus, #reviewComment:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2);
  outline: none;
}